/* eslint-disable camelcase */
import React, { Component } from 'react';
import { Table, Tag, Icon } from 'antd';
import { URLS_NAV } from '@/constants';
import DIFFICULTY from '../DIFFICULTY';
import s from './index.less';

class ProblemList extends Component {
  setTableColumns = () => {
    const { tagToggle } = this.props;
    if (tagToggle) {
      this.columns = [
        {
          title: '',
          key: 'my_status',
          dateIndex: 'my_status',
          width: 20,
          render: (_, record) => {
            const { my_status } = record;
            if (my_status === 0) {
              return <Icon type="check" style={{ color: '#52c41a' }} />;
            }
            if (my_status === null || my_status === undefined) {
              return null;
            }
            return <Icon type="minus" style={{ color: '#d9120d' }} />;
          },
        },
        {
          title: '#',
          key: '_id',
          dataIndex: '_id',
          width: 80,
          render: (_, record) => {
            const { _id } = record;
            return (
              <span className={s.clickable} onClick={() => this.handleProblemClick(_id)}>
                {_id}
              </span>
            );
          },
        },
        {
          title: '题目',
          key: 'title',
          dataIndex: 'title',
          width: 300,
          render: (_, record) => {
            const { _id, title } = record;
            return (
              <span className={s.clickable} onClick={() => this.handleProblemClick(_id)}>
                {title}
              </span>
            );
          },
        },
        {
          title: '难度',
          key: 'difficulty',
          dataIndex: 'difficulty',
          render: (_, record) => (
            <Tag color={DIFFICULTY[record.difficulty].color}>
              {DIFFICULTY[record.difficulty].text}
            </Tag>
          ),
        },
        {
          title: '标签',
          key: 'tags',
          dataIndex: 'tags',
          render: (_, record) => <span>{record.tags.slice(0, 3).join('、')}</span>,
        },
        {
          title: '提交总数',
          key: 'submission_number',
          dataIndex: 'submission_number',
          width: 100,
        },
        {
          title: '通过率',
          key: 'passing_rate',
          dataIndex: 'passing_rate',
          width: 100,
        },
      ];
    } else {
      this.columns = [
        {
          title: '#',
          key: '_id',
          dataIndex: '_id',
          width: 80,
        },
        {
          title: '题目',
          key: 'title',
          dataIndex: 'title',
          width: 400,
        },
        {
          title: '难度',
          key: 'difficulty',
          dataIndex: 'difficulty',
          render: (_, record) => (
            <Tag color={DIFFICULTY[record.difficulty].color}>
              {DIFFICULTY[record.difficulty].text}
            </Tag>
          ),
        },
        {
          title: '提交总数',
          key: 'submission_number',
          dataIndex: 'submission_number',
          width: 120,
        },
        {
          title: '通过率',
          key: 'passing_rate',
          dataIndex: 'passing_rate',
          width: 120,
        },
      ];
    }
  };

  handlePageChange = (page, pageSize) => {
    const { updatePagesInfo } = this.props;
    updatePagesInfo({ current: page, pageSize });
  };

  handleProblemClick = id => {
    window.location.href = `${URLS_NAV.problem}${id}`;
  };

  render() {
    this.setTableColumns();
    const { dataSource, total, pageSize, current } = this.props;
    return (
      <Table
        className={s.container}
        columns={this.columns}
        dataSource={dataSource}
        pagination={{
          showQuickJumper: true,
          current,
          pageSize,
          total,
          onChange: this.handlePageChange,
          onShowSizeChange: this.handlePageChange,
          showSizeChanger: true,
          pageSizeOptions: ['10', '20', '30', '50'],
        }}
      />
    );
  }
}

export default ProblemList;
