import React, { Component } from 'react';
import { Tag, Divider, Button, Row } from 'antd';
import s from './index.less';

const { CheckableTag } = Tag;

class ProblemTag extends Component {
  handleCheckedChange = name => {
    const { updateSelectedTag, tagSelected } = this.props;
    const currentIndex = tagSelected.indexOf(name);
    const resultSelectedTag = tagSelected.map(item => item);
    if (currentIndex === -1) {
      resultSelectedTag.push(name);
    } else {
      resultSelectedTag.splice(currentIndex, 1);
    }
    updateSelectedTag([...new Set(resultSelectedTag)]);
  };

  handleButtonClick = () => {
    const { randomProblem } = this.props;
    randomProblem();
  };

  tagWithSelected = () => {
    const { tagList, tagSelected } = this.props;
    let { commonTag = [], timeTag = [], sourceTag = [], knowledgeTag = [] } = tagList;
    commonTag = commonTag.map(item => {
      if (tagSelected.indexOf(item.name) !== -1) {
        return {
          ...item,
          checked: true,
        };
      }
      return {
        ...item,
        checked: false,
      };
    });
    knowledgeTag = knowledgeTag.map(item => {
      if (tagSelected.indexOf(item.name) !== -1) {
        return {
          ...item,
          checked: true,
        };
      }
      return {
        ...item,
        checked: false,
      };
    });
    timeTag = timeTag.map(item => {
      if (tagSelected.indexOf(item.name) !== -1) {
        return {
          ...item,
          checked: true,
        };
      }
      return {
        ...item,
        checked: false,
      };
    });
    sourceTag = sourceTag.map(item => {
      if (tagSelected.indexOf(item.name) !== -1) {
        return {
          ...item,
          checked: true,
        };
      }
      return {
        ...item,
        checked: false,
      };
    });
    return {
      commonTag,
      timeTag,
      sourceTag,
      knowledgeTag,
    };
  };

  render() {
    const {
      commonTag = [],
      timeTag = [],
      sourceTag = [],
      knowledgeTag = [],
    } = this.tagWithSelected();
    return (
      <section className={s.container}>
        <Row type="flex" justify="center">
          <Divider>常见标签</Divider>
          <div>
            {commonTag.map(item => (
              <CheckableTag
                checked={item.checked}
                key={item.id}
                onChange={() => this.handleCheckedChange(item.name, 'commonTag')}
              >
                {item.name}
              </CheckableTag>
            ))}
          </div>
          <Divider>知识点类型</Divider>
          <div>
            {knowledgeTag.map(item => (
              <CheckableTag
                checked={item.checked}
                key={item.id}
                onChange={() => this.handleCheckedChange(item.name, 'knowledgeTag')}
              >
                {item.name}
              </CheckableTag>
            ))}
          </div>
          <Divider>试题来源</Divider>
          <div>
            {sourceTag.map(item => (
              <CheckableTag
                checked={item.checked}
                key={item.id}
                onChange={() => this.handleCheckedChange(item.name, 'sourceTag')}
              >
                {item.name}
              </CheckableTag>
            ))}
          </div>
          <Divider>时间</Divider>
          <div>
            {timeTag.map(item => (
              <CheckableTag
                checked={item.checked}
                key={item.id}
                onChange={() => this.handleCheckedChange(item.name, 'timeTag')}
              >
                {item.name}
              </CheckableTag>
            ))}
          </div>
          <Button
            htmlType="button"
            icon="shuffle"
            style={{ marginTop: `20px` }}
            onClick={this.handleButtonClick}
          >
            随机选择
          </Button>
        </Row>
      </section>
    );
  }
}

export default ProblemTag;
