import React from 'react';
import { Card, Tag, Icon } from 'antd';
import Link from '~/Link';
import s from './index.less';
import { URLS_NAV } from '@/constants';

const STATUS = {
  '-1': <Tag color="red">已经结束</Tag>,
  '0': <Tag color="orange">正在进行</Tag>,
  '1': <Tag color="green">即将开始</Tag>,
};

const ContestType = {
  '0': '公开赛',
  '1': '邀请赛',
};

function format(timestamp) {
  const date = new Date(timestamp);
  return `${new Date(date).toLocaleDateString().slice(5)} ${new Date(date)
    .toTimeString()
    .slice(0, 5)}`;
}

function ProblemCard(props) {
  const { data } = props;
  const { title, status, startTime, endTime, id, ruleType, contestType } = data;
  const start = format(startTime);
  const end = format(endTime);

  return (
    <Link to={`${URLS_NAV.contest}${id}`} className={s.container}>
      <Card title={title} bordered={false}>
        <div>
          <Tag color="blue">
            <Icon type="trophy" theme="filled" /> {ruleType}
          </Tag>
          {ContestType[contestType]}
        </div>
        <div>{STATUS[status]}</div>
        <div>
          {/* <Icon type="calendar" style={{ color: '#1890ff' }} /> */}
          开始: {start}
        </div>
        <div>
          {/* <Icon type="clock-circle" style={{ color: '#f5222d' }} /> */}
          结束: {end}
        </div>
      </Card>
    </Link>
  );
}

export default React.memo(ProblemCard);
