import { observable, action, runInAction } from 'mobx';
import api from '@/services/api';

export default class problem {
  @observable loading = false;

  @observable problem = {
    list: [],
    pageSize: 20,
    total: 0,
    current: 1,
    recent: '',
    difficulty: '',
    searchContent: '',
    tagToggle: true,
    tagSelected: [],
  };

  @observable tag = {};

  @action.bound
  updatePagesInfo({ current, pageSize } = {}) {
    this.problem.current = current;
    this.problem.pageSize = pageSize;
    this.getProblemList({ page: current, limit: pageSize });
  }

  @action.bound
  updateDifficulty(difficulty) {
    this.problem.difficulty = difficulty;
    this.getProblemList();
  }

  @action.bound
  resetPageInfo() {
    this.problem = {
      list: [],
      pageSize: 20,
      total: 0,
      current: 1,
      recent: this.problem.recent,
      difficulty: '',
      searchContent: '',
      tagToggle: this.problem.tagToggle,
      tag: [],
      tagSelected: [],
    };
    this.getProblemList();
  }

  @action.bound
  updateSearchContent(content) {
    this.problem.searchContent = content;
    this.getProblemList();
  }

  @action.bound
  updateSelectedTag(tag = []) {
    this.problem.tagSelected = tag;
    this.getProblemList();
  }

  @action.bound
  async getTagList() {
    try {
      const tagList = await api.fetchTagList();
      runInAction(() => {
        this.tag = tagList;
      });
    } catch (e) {
      console.log(`getTag error, ${e}`);
    }
  }

  @action.bound
  updateTagToggle(value) {
    this.problem.tagToggle = value;
  }

  @action.bound
  updateRecent(value) {
    this.problem.recent = value;
  }

  @action
  async getProblemList() {
    this.loading = true;
    const { pageSize, current, difficulty, tagSelected, searchContent, recent } = this.problem;
    try {
      const problemList = await api.fetchProblemList({
        difficulty,
        recent,
        paging: true,
        offset: pageSize * (current - 1),
        limit: pageSize,
        page: current,
        tag: `[${tagSelected.toString()}]`,
        keyword: searchContent,
      });
      runInAction(() => {
        this.problem.list = problemList.results.map(item => ({
          ...item,
          passing_rate:
            item.accepted_number === 0
              ? `0.0%`
              : `${((item.accepted_number * 100) / item.submission_number).toFixed(2)}%`,
        }));
        this.problem.total = problemList.total;
        this.loading = false;
      });
    } catch (e) {
      console.log(`getProblem error, ${e}`);
    }
  }
}
