import React from 'react';
import cn from 'classnames';
import { Icon, Tag } from 'antd';
import { URLS_USER } from '@/constants';
import Link from '~/Link';
import s from './index.less';

// "rank_up_down": 1 //1排名上升，0排名不变，-1排名下降
const updownIcon = {
  '1': <Icon type="arrow-up" style={{ color: '#f00' }} />,
  '-1': <Icon type="arrow-down" style={{ color: '#0f0' }} />,
};

function User(props) {
  const { data, idx } = props;
  const { realName, totalScore, rankUpDown, userName } = data;
  const cls = cn({
    [`${s.first}`]: idx === 1,
    [`${s.second}`]: idx === 2,
    [`${s.third}`]: idx === 3,
    [s.rank]: true,
  });

  return (
    <Link to={`${URLS_USER.center}?username=${userName}`} key={idx}>
      <div className={s.user}>
        <span className={cls}>{idx}</span>
        <span className={s.name}>{realName}</span>
        <span className={s.score}>{totalScore}</span>
        {rankUpDown ? <span>{updownIcon[rankUpDown]}</span> : <span />}
      </div>
    </Link>
  );
}

const RankUser = React.memo(User);

function SideRank(props) {
  const { rankProfile } = props;
  if (!rankProfile || !rankProfile.length) return null;

  return (
    <div className={s.container}>
      <h2>大神榜</h2>
      {rankProfile.map((item, i) => (
        <RankUser data={item} idx={i + 1} key={item.id} />
      ))}
    </div>
  );
}

export default SideRank;
