import React from 'react';
import s from './index.less';
import { DAYZN } from '@/constants';

function CountDown(props) {
  const { nowTime, countDown } = props;
  if (!nowTime) return null;

  const time = new Date(nowTime);
  const day = DAYZN[time.getDay()];
  const date = time.toLocaleDateString();
  const [y, m, d] = date.split('/');

  // 只有日期
  if (!countDown)
    return (
      <div className={s.card}>
        <h2>{y}</h2>
        <h2>
          {m}月{d}日
        </h2>
        <h2>{day}</h2>
      </div>
    );

  const { taskName, days } = countDown;

  // 日期和倒计时
  return [
    <div className={s.card} key="card">
      <p>距离</p>
      <p className={s.color}>{taskName}</p>
      <p>还剩</p>
      <p className={s.color}>{days}</p>
      <p>天</p>
    </div>,
    <p className={s.date} key="date">
      {date} {day}
    </p>,
  ];
}

export default React.memo(CountDown);
