import React from 'react';
import { Link } from 'react-router-dom';

export default function OLink(props) {
  const { to } = props;
  if (typeof to === 'string' && /^https?:\/\//.test(to)) {
    return (
      <a href={to} {...props}>
        {props.children}
      </a>
    );
  }
  return <Link {...props} />;
}
