import React, { PureComponent } from 'react';
import { Carousel, Icon } from 'antd';
import s from './index.less';

class Announcement extends PureComponent {
  C = React.createRef();

  handlePrev = () => {
    const cInstance = this.C.current;
    cInstance && cInstance.prev();
  };

  handleNext = () => {
    const cInstance = this.C.current;
    cInstance && cInstance.next();
  };

  render() {
    const { announcement = [] } = this.props;
    if (!announcement || !announcement.length) return null;

    return (
      <div className={s.announcement}>
        <Carousel autoplay ref={this.C}>
          {announcement.map(item =>
            item.olink ? (
              <a href={item.olink} key={item.oimage}>
                <img src={item.oimage} alt="announcement" className={s.slide} />
              </a>
            ) : (
              <img src={item.oimage} alt="announcement" className={s.slide} key={item.oimage} />
            )
          )}
        </Carousel>
        <Icon type="left" onClick={this.handlePrev} className={s.prev} />
        <Icon type="right" onClick={this.handleNext} className={s.next} />
      </div>
    );
  }
}

export default Announcement;
