import { observable, action, runInAction } from 'mobx';
import api from '@/services/api';

export default class Home {
  @observable data = {};

  @action
  async getData() {
    try {
      const data = await api.getHome();
      runInAction(() => {
        this.data = data;
      });
    } catch (error) {
      console.log('getHome error', error);
    }
  }
}
