import React, { Component } from 'react';
import { Dropdown, Menu, Icon, Row, Col, Switch, Input, Button } from 'antd';
import DIFFICULTY from '../DIFFICULTY';
import s from './index.less';

class ProblemHeader extends Component {
  handleSearch = value => {
    const { updateSearchContent } = this.props;
    updateSearchContent(value);
  };

  handleDifficultyChange = value => {
    const { updateDifficulty } = this.props;
    updateDifficulty(value);
  };

  handleToggleTag = value => {
    const { updateTagToggle } = this.props;
    updateTagToggle(value);
  };

  handleReset = () => {
    const { resetPageInfo } = this.props;
    resetPageInfo();
  };

  renderMenu = () => (
    <Menu>
      <Menu.Item onClick={() => this.handleDifficultyChange()}>全部</Menu.Item>
      {Object.keys(DIFFICULTY).map(item => (
        <Menu.Item key={DIFFICULTY[item].text} onClick={() => this.handleDifficultyChange(item)}>
          {DIFFICULTY[item].text}
        </Menu.Item>
      ))}
    </Menu>
  );

  render() {
    const { difficulty, tagToggle } = this.props;
    return (
      <div className={s.container}>
        <Row type="flex" align="middle">
          <Col>
            <h2>题目列表</h2>
          </Col>
          <Col offset={5}>
            <Dropdown overlay={this.renderMenu()}>
              <Button htmlType="button">
                {(DIFFICULTY[difficulty] && DIFFICULTY[difficulty].text) || '难度'}{' '}
                <Icon type="down" />
              </Button>
            </Dropdown>
          </Col>
          <Col offset={1}>
            <Switch
              checkedChildren="标签"
              unCheckedChildren="标签"
              defaultChecked={tagToggle}
              style={{ marginTop: `5px` }}
              onChange={this.handleToggleTag}
            />
          </Col>
          <Col offset={1}>
            <Input.Search
              placeholder="请输入关键字"
              onSearch={value => this.handleSearch(value)}
              enterButton
            />
          </Col>
          <Col offset={1}>
            <Button htmlType="button" type="circle" icon="redo" onClick={this.handleReset} />
          </Col>
        </Row>
      </div>
    );
  }
}

export default ProblemHeader;
