import memoizeOne from 'memoize-one';
import isEqual from 'lodash/isEqual';
import { observable, action } from 'mobx';

export default class Menu {
  @observable data = [];

  @action
  getData(routes) {
    const menuData = filterMenuData(memoizeOneFormatter(routes));
    this.data = menuData;
  }
}
// Conversion router to menu.
function formatter(data) {
  return data
    .map(item => {
      if (!item.name || !item.path) {
        return null;
      }

      // https://github.com/ant-design/ant-design-pro/issues/3178
      const result = {
        ...item,
      };
      // 将routes字段转为children字段
      if (item.routes) {
        const children = formatter(item.routes);
        // Reduce memory usage
        result.children = children;
      }
      delete result.routes;
      return result;
    })
    .filter(item => item);
}

const memoizeOneFormatter = memoizeOne(formatter, isEqual);

/**
 * filter menuData
 */
const filterMenuData = menuData => {
  if (!menuData) {
    return [];
  }
  return menuData.filter(item => item.name && !item.hideInMenu).filter(item => item);
};
