import React, { Component } from 'react';
import ProblemList from '~/p_problem/ProblemList';
import ProblemTag from '~/p_problem/ProblemTag';
import ProblemHeader from '~/p_problem/ProblemHeader';
import { Row, Col, Spin } from 'antd';
import { observer, inject } from 'mobx-react';
import queryString from 'query-string';
import s from './index.less';
import api from '@/services/api';
import { URLS_NAV } from '@/constants';

@inject('problemStore')
@observer
class Problem extends Component {
  constructor(props) {
    super(props);
    const { location, problemStore } = props;
    const params = queryString.parse(location.search);
    const { recent = '' } = params;
    problemStore.updateRecent(recent);
    problemStore.getTagList();
    problemStore.getProblemList({ recent });
  }

  randomProblem = () => {
    api.pickOne().then(data => {
      window.location.href = `${URLS_NAV.problem}${data}`;
    });
  };

  render() {
    const { problemStore } = this.props;
    const { problem, tag, loading } = problemStore;
    const {
      list,
      total,
      pageSize,
      current,
      tagToggle,
      searchContent,
      difficulty,
      tagSelected,
    } = problem;
    const {
      updatePagesInfo,
      updateDifficulty,
      updateSelectedTag,
      updateSearchContent,
      resetPageInfo,
      updateTagToggle,
    } = problemStore;
    return (
      <div className={s.container}>
        <Spin spinning={loading}>
          <Row type="flex">
            <Col span={18}>
              <ProblemHeader
                tagToggle={tagToggle}
                searchContent={searchContent}
                difficulty={difficulty}
                updateDifficulty={updateDifficulty}
                updateSearchContent={updateSearchContent}
                resetPageInfo={resetPageInfo}
                updateTagToggle={updateTagToggle}
              />
              <ProblemList
                dataSource={list}
                tagToggle={tagToggle}
                total={total}
                current={current}
                pageSize={pageSize}
                updatePagesInfo={updatePagesInfo}
                updateDifficulty={updateDifficulty}
              />
            </Col>
            <Col span={6}>
              <ProblemTag
                tagList={tag}
                randomProblem={this.randomProblem}
                updateSelectedTag={updateSelectedTag}
                tagSelected={tagSelected}
              />
            </Col>
          </Row>
        </Spin>
      </div>
    );
  }
}

export default Problem;
