import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import { Layout } from 'antd';
import 'intersection-observer';
import quicklink from 'quicklink/dist/quicklink.mjs';
import s from './index.less';
import HeaderRight from './HeaderRight';
import BaseMenu from './BaseMenu';
import { URLS_USER } from '@/constants';
import { logout } from '@/utils/utils';
import logo from '../../assets/logo.png';

const { Header } = Layout;

@inject('userStore')
@inject('menuStore')
@observer
class GlobalHeader extends Component {
  componentDidMount() {
    const { userStore, menuStore, routes } = this.props;
    // 获取用户身份信息
    userStore.getProfile();
    // 根据route初始化menu数据
    menuStore.getData(routes);

    // 预取links
    const { data: menuData } = menuStore;
    const preLinks = menuData.reduce((re, cur) => {
      if (cur.path && /^https?:\/\//.test(cur.path)) {
        re.push(cur.path);
      }
      return re;
    }, []);
    quicklink({ urls: preLinks });
  }

  handleMenuClick = ({ key }) => {
    const { userStore } = this.props;
    if (URLS_USER[key]) {
      window.location.href = URLS_USER[key];
      return;
    }
    if (key === 'logout') {
      logout();
      userStore.clearProfile();
    }
  };

  render() {
    const { userStore, menuStore } = this.props;
    const { currentUser } = userStore;
    const { data: menuData } = menuStore;
    const { real_name: realName, avatar } = currentUser || {};

    return (
      <Header className={s.fixedHeader}>
        <div className={s.logo}>
          <img src={logo} alt="logo" />
        </div>
        <BaseMenu {...this.props} menuData={menuData} />
        <HeaderRight realName={realName} avatar={avatar} onMenuClick={this.handleMenuClick} />
      </Header>
    );
  }
}

export default GlobalHeader;
