import React from 'react';
import { Menu, Icon, Avatar, Button } from 'antd';
import HeaderDropdown from './HeaderDropdown';
import styles from './HeaderRight.less';

function HeaderRight(props) {
  const { realName, avatar, onMenuClick } = props;
  const menu = (
    <Menu className={styles.menu} selectedKeys={[]} onClick={onMenuClick}>
      <Menu.Item key="center">
        <Icon type="user" />
        我的主页
      </Menu.Item>
      <Menu.Item key="submissions">
        <Icon type="profile" />
        我的提交
      </Menu.Item>
      <Menu.Item key="setting">
        <Icon type="setting" />
        我的设置
      </Menu.Item>
      <Menu.Divider />
      <Menu.Item key="logout">
        <Icon type="logout" />
        退出
      </Menu.Item>
    </Menu>
  );

  return (
    <div className={styles.right}>
      {realName ? (
        <HeaderDropdown overlay={menu}>
          <span className={`${styles.action} ${styles.account}`}>
            <Avatar size="small" className={styles.avatar} src={avatar} alt="avatar" />
            <span className={styles.name}>{realName}</span>
            <Icon type="caret-down" />
          </span>
        </HeaderDropdown>
      ) : (
        <Button href={`https://ke.youdao.com/login?back_url=${window.location.href}`}>登录</Button>
      )}
    </div>
  );
}

export default HeaderRight;
