import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import { Row, Col } from 'antd';
import Banner from '~/p_home/Banner';
import SideRank from '~/p_home/SideRank';
import Section from '~/p_home/Section';
import ProblemCard from '~/p_home/ProblemCard';
import ContestCard from '~/p_home/ContestCard';
import { URLS_NAV } from '@/constants';
import s from './index.less';

@inject('homeStore')
@observer
class Home extends Component {
  componentDidMount() {
    const { homeStore } = this.props;
    homeStore.getData();
  }

  render() {
    const { homeStore } = this.props;
    const { announcement, countDown, problem, contest, nowTime, rankProfile } =
      homeStore.data || {};
    return (
      <div className={s.container}>
        <Banner announcement={announcement} countDown={countDown} nowTime={nowTime} />
        <Row>
          <Col span={6}>
            <SideRank rankProfile={rankProfile} />
          </Col>
          <Col span={18}>
            <Section
              list={problem}
              title="近期题库更新"
              component={ProblemCard}
              moreLink="/problems?recent=0"
            />
            <Section
              list={contest}
              title="近期赛事"
              component={ContestCard}
              moreLink={URLS_NAV.contests}
            />
          </Col>
        </Row>
      </div>
    );
  }
}

export default Home;
