import request from '@/utils/request';

export default {
  getUserInfo(username = undefined) {
    return request('profile', 'get', {
      params: {
        username,
      },
    });
  },
  getHome() {
    return request('home', 'get');
  },
  fetchTagList() {
    return request('problem/tags', 'get');
  },
  pickOne() {
    return request('pickone', 'get');
  },
  fetchProblemList({
    paging = true,
    offset = 0,
    limit = 20,
    page = 0,
    rule = '',
    standard = 'down',
    tag = '[]',
    difficulty = '',
    keyword = '',
    recent = '',
  }) {
    return request('problem', 'get', {
      params: {
        paging,
        page,
        offset,
        limit,
        rule,
        standard,
        tag,
        difficulty,
        keyword,
        recent,
      },
    });
  },
};
