import React from 'react';
import { Icon } from 'antd';
import { ReactComponent as rankIc } from '@/assets/rank.svg';
import { ReactComponent as pulseIc } from '@/assets/ios-pulse-strong.svg';
import { ReactComponent as keypadIc } from '@/assets/ios-keypad.svg';

const customIc = {
  rank: rankIc,
  pulse: pulseIc,
  keypad: keypadIc,
};

// 自定义icon
function OIcon(props) {
  const { type, ...rest } = props;

  if (customIc[type]) {
    return <Icon component={customIc[type]} {...rest} />;
  }

  return <Icon {...props} />;
}

export default React.memo(OIcon);
