import React from 'react';
import { Card, Tag } from 'antd';
import Link from '~/Link';
import s from './index.less';
import { URLS_NAV } from '@/constants';

const difTag = {
  High: <Tag color="red">超级难</Tag>,
  Mid: <Tag color="orange">普及</Tag>,
  Low: <Tag color="green">入门难度</Tag>,
};

function ProblemCard(props) {
  const { data } = props;
  const { title, tag, ruleType, difficulty, displayId } = data;

  return (
    <Link to={`${URLS_NAV.problem}${displayId}`} className={s.container}>
      <Card title={title} bordered={false}>
        <div>
          题目ID：
          <Tag color="purple">{displayId}</Tag>
        </div>
        <div>
          难度：
          {difTag[difficulty]}
        </div>
        <div>
          题型：
          <Tag color="magenta">{ruleType}</Tag>
        </div>
        <div>
          知识点：
          <Tag color="geekblue">{tag}</Tag>
        </div>
      </Card>
    </Link>
  );
}

export default ProblemCard;
